.header {
   display: flex;
   justify-content: space-between;
   font-weight: 600;
   height: 50px;
   align-items: center;
   text-align-last: left;
   z-index: 2;
   background: var(--header);
   padding: 5px;
   border-radius: 100px;
   backdrop-filter: blur(20px);
   &__right {
      display: flex;
      align-items: center;
      gap: 5px;
   }
   &__logo {
      height: 40px;
      width: 80px;
      font-weight: 900;
      font-size: 22px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var(--mainColor);
      color: var(--white-p);
      border-radius: 20px;
      font-style: italic;
   }
   &__icon {
      width: 20px;
      height: 20px;
      cursor: pointer;
      color: var(--black-p);
      fill: var(--black-p);
      div {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      svg {
         width: 20px;
         height: 20px;
      }
   }
   &__social {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      position: relative;
      z-index: 2;
      color: var(--white-p);
      background: var(--mainColor);
      border-radius: 50%;
      svg {
         width: 25px;
         height: 25px;
      }
      &::after {
         content: '';
         height: 100%;
         width: 100%;
         border-radius: 50%;
         position: absolute;
         top: 0;
         left: 0;
         z-index: -1;
         opacity: 0;
         transition: opacity 0.2s;
         background: radial-gradient(
            circle at 30% 110%,
            #ffdb8b 0%,
            #ee653d 25%,
            #d42e81 50%,
            #a237b6 75%,
            #3e5fbc 100%
         );
      }

      &:hover::after {
         opacity: 1;
      }
   }

   &__mode-btn {
      height: 40px;
      width: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50px;
      cursor: pointer;
      background: var(--mainColor);
      position: relative;
   }
   &__icon-mode {
      pointer-events: none;
      color: var(--white-p);
      height: 25px;
      width: 25px;
      position: absolute;
      top: 7.5px;
      left: 7.5px;
      div {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      svg {
         height: 25px;
         width: 25px;
      }
   }
}
.header__dark {
   opacity: 0;
   transition: 0.1s;
}
.header__light {
   opacity: 1;
   transition: 0.1s;
}
body.darkmode .header__dark {
   opacity: 1;
}
body.darkmode .header__light {
   opacity: 0;
}
